*{margin: 0;padding: 0;}
img{display: block;}
li{list-style: none;}
a{text-decoration: none;}

.header{position: fixed; height: 60px; left: 0; top: 0; width: 100%; z-index: 9998; background-color: #fff; border-bottom: 1px solid #eee;}
.header .topLogo{position: absolute; left: 5px;top: 15px;}
.header .topLogo img{height: 30px;}
.header .gnbBtn{position: absolute; right: 5px; top: 20px; z-index: 9999;}

.gnb{position: fixed; top: 60px; right: -200px; width: 200px; height: calc(100vh - 60px);background-color: #000; z-index: 9999; overflow: hidden; transition-duration: 0.5s;} 
.gnb a{display: block; color: #fff; text-align: center; line-height: 40px; text-decoration: none;} 


.topBanner{position: relative; margin-top: 60px;}
.topBanner .topBg{width: 100%;}
.topBanner .topList li{position: absolute; width: 100%; top: 0; left: 0;}
.topBanner .topList li img{width: 100%;}
.prev{position: absolute; top: 50%; left: 10px; transform: translateY(-50%); cursor: pointer;}
.next{position: absolute; top: 50%; right: 10px; transform: translateY(-50%); cursor: pointer;}


@media screen and (min-width:600px) {
.best h2{font-size: 20px; line-height: 80px; text-align: center;}
.best ul{display: flex; justify-content: space-between; width: 94%; margin: 0 auto;}  
.best ul li{width: 24%; text-align: center;}
.best ul li img{width: 100%;}
.best ul li h3{font-size: 13px; line-height: 2em;}
.best ul li p{font-size: 12px; white-space: nowrap; overflow: hidden; text-overflow: ellipsis;}
}
@media screen and (max-width:599px) {
  .best h2{font-size: 20px; line-height: 80px; text-align: center;}
  .best ul{width: 94%; margin: 0 auto;}  
  .best ul li{text-align: center; display: flex; align-items: center; justify-content: space-between; margin-bottom: 10px;}
  .best ul li img{width: 50%;}
  .best ul li div{width: 45%; display: flex; flex-direction: column; justify-content: center;}
  .best ul li h3{font-size: 13px; line-height: 2em;}
  .best ul li p{font-size: 12px; white-space: nowrap; overflow: hidden; text-overflow: ellipsis;}
  }

.mdChoice{position: relative; margin: 50px auto; width: 94%;}
.mdChoice h2{text-align: center;}
.mdChoice .tabsTitle{display: flex;}
.mdChoice .tabsTitle li{width: 50%; text-align: center; background-color: #eee; line-height: 50px; margin-bottom: 20px;}
.mdChoice .tabsTitle li.select{background-color: #222; color: #fff;}
.mdChoice .tabsCon div{display: none;}
.mdChoice .tabsCon div.select{display: block;}
.mdChoice .tabsCon img{width: 100%;}



