.BottomBanner{position: relative; margin: 50px 0; height: 700px; background-image: url('./img/halloween-min.gif'); background-size: cover; background-attachment: fixed; background-position: center;}

.footer{width: 94%; margin: 0 auto;}


.lightbox{position: fixed; left: 0; top: 0; width: 100%; height: 100%; background-color: rgba(0,0,0,0.8);}
.lightInner{position: absolute; left: 50%; top: 50%; width: 70%; padding: 20px; background-color: #fff; border-radius: 20px; transform: translate(-50%,-50%); display: flex; }
.lightInner img{width: 50%;}
.lightInner .lightText{width: 50%; display: flex; text-align: center; flex-direction: column; align-items: center; justify-content: center;}
.lightInner .lightText h2{font-size: 18px;}
.lightInner .lightText .close{position: absolute; right: 20px; bottom: 20px; background-color: #000; color: #fff; width: 60px; height: 26px;}

.accordian{position: relative; background-color: #eee; margin: 100px 0; padding: 50px 0;}
.accordian h2{text-align: center; margin-bottom: 50px;}
.accordian ul{width: 90%; margin: 0 auto;}
.accordian li{margin-bottom: 5px;}
.accordian li h3{padding: 10px; background-color: #000; color: #fff; border-radius: 10px; font-size: 14px;}
.accordian li div{padding: 0px 10px; height: 0; font-size: 14px; overflow: hidden;  transition-duration: 0.7s;}

.visited{position: relative; width: 90%; margin: 100px auto;}
.visited h2{text-align: center; margin-bottom: 25px;}
.visited .inPutBox{position: relative; display: flex;}
.visited .inPutBox .inPutText{width: 80%; height: 30px;}
.visited .inPutBox .submit{width: 20%; background-color: #000; color: #fff; border: none;}
.visited ul li{padding: 10px; box-sizing: border-box; border: 1px solid #eee; margin-bottom: 2px; display: flex; justify-content: space-between;}
.visited ul li .close{display: block; width: 15px; height: 15px; background-color: #000; color: #fff; line-height: 15px; text-align: center; border-radius:8px; font-size: 10px;}

.about_box{position: relative; width: 90%; margin: 100px auto;}
.about_box .about_title{text-align: center; margin-bottom: 50px; font-weight: 500;}
.about_box .top_infobox{margin-bottom: 100px;}
.about_box .top_infobox p{text-align: center; line-height: 1.8em;}
.about_box .contact{position: relative;}
.about_box .contact > p{text-align: center; line-height: 1.8em;}
.about_box .contact p span{font-weight: 700;}
.about_box .contact h3{text-align: center; margin: 25px 0;}
.about_box .contact ul{text-align: center;}
.about_box .contact ul li{margin-bottom: 10px;}
.about_box img{width: 100%; margin: 50px auto;  display: block;}


.NewProduct{position: relative; width: 90%; margin: 100px auto; overflow: hidden;}
.NewProduct h2{font-size: 20px; margin-bottom: 50px; text-align: center;}

.NewWrap{position: relative; }
.NewWrap ul{position: relative; width: 266.64%; overflow: hidden;}
.NewWrap ul li{width: 12.5%; float: left;}
.NewWrap ul li img{width: 90%; margin: 0 auto; display: block;}
.NewWrap .NewPrev{position: absolute; left: 10px; top: 50%; transform: translateY(-50%);}
.NewWrap .NewNext{position: absolute; right: 10px; top: 50%; transform: translateY(-50%);}